// app/admin/dashboard/page.tsx
'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import {
  Package,
  Users,
  ShoppingBag,
  DollarSign,
  TrendingUp,
  TrendingDown,
  Home,
  CreditCard,
  BarChart3,
  Settings,
  LogOut,
  ChevronDown,
  MoreVertical,
  Calendar,
  Mail,
  Shield,
  Tag
} from 'lucide-react'

interface DashboardStats {
  total_users: number
  total_products: number
  total_orders: number
  total_revenue: number
  today_sales?: number
  today_revenue?: number
  pending_amount?: number
  monthly_data?: MonthlyRevenue[]
}

interface MonthlyRevenue {
  month: string
  revenue: number
  orders: number
  profit: number
  loss: number
}

interface RecentOrder {
  _id?: string
  order_id: string
  user_email?: string
  customer_name?: string
  items?: any[]
  product_names?: string
  total_amount: number
  order_status: string
  created_at: string
}

export default function AdminDashboard() {
  const [stats, setStats] = useState<DashboardStats | null>(null)
  const [recentOrders, setRecentOrders] = useState<RecentOrder[]>([])
  const [loading, setLoading] = useState(true)
  const [selectedPeriod, setSelectedPeriod] = useState('Last 30 days')
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false)
  const router = useRouter()

  // Calculate monthly revenue data for chart
  const getMonthlyData = () => {
    // Sample data - replace with actual API data
    return [
      { month: 'Jan', profit: 95000, loss: 75000 },
      { month: 'Feb', profit: 88000, loss: 62000 },
      { month: 'Mar', profit: 92000, loss: 45000 },
      { month: 'Apr', profit: 78000, loss: 98000 },
      { month: 'May', profit: 85000, loss: 65000 },
      { month: 'Jun', profit: 52000, loss: 42000 },
      { month: 'Jul', profit: 72000, loss: 48000 },
      { month: 'Aug', profit: 90000, loss: 58000 },
      { month: 'Sep', profit: 88000, loss: 52000 },
    ]
  }

  useEffect(() => {
    checkAdminAndFetchData()
  }, [])

  const checkAdminAndFetchData = async () => {
    const token = localStorage.getItem('token')
    if (!token) {
      router.push('/login')
      return
    }

    try {
      const res = await fetch('http://localhost:8000/api/admin/dashboard', {
        headers: { 'Authorization': `Bearer ${token}` }
      })

      if (res.status === 403) {
        alert('Admin access required')
        router.push('/')
        return
      }

      if (res.ok) {
        const data = await res.json()
        // Add calculated fields
        const enhancedStats = {
          ...data.stats,
          today_sales: data.stats.total_orders * 0.1, // Calculate from today's orders
          today_revenue: data.stats.total_revenue * 0.05, // Sample calculation
          pending_amount: data.stats.total_revenue * 0.2, // Sample calculation
          monthly_data: getMonthlyData()
        }
        setStats(enhancedStats)
        setRecentOrders(data.recent_orders || [])
      }
    } catch (error) {
      console.error('Error fetching dashboard:', error)
      alert('Failed to load dashboard data')
    } finally {
      setLoading(false)
    }
  }

  const handleLogout = () => {
    localStorage.removeItem('token')
    router.push('/login')
  }

  const getStatusColor = (status: string) => {
    switch(status.toLowerCase()) {
      case 'delivered': return 'bg-green-500/20 text-green-500'
      case 'pending': return 'bg-yellow-500/20 text-yellow-500'
      case 'processing': return 'bg-blue-500/20 text-blue-500'
      case 'cancelled': case 'canceled': return 'bg-red-500/20 text-red-500'
      default: return 'bg-gray-500/20 text-gray-500'
    }
  }

  const getStatusDotColor = (status: string) => {
    switch(status.toLowerCase()) {
      case 'delivered': return 'bg-green-500'
      case 'pending': return 'bg-yellow-500'
      case 'processing': return 'bg-blue-500'
      case 'cancelled': case 'canceled': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-[#1a1d2e] flex items-center justify-center">
        <div className="text-white text-xl">Loading dashboard...</div>
      </div>
    )
  }

  return (
    <div className="flex h-screen bg-[#1a1d2e]">
      {/* Sidebar */}
      <div className={`${sidebarCollapsed ? 'w-20' : 'w-64'} bg-[#2a2d47] transition-all duration-300 flex flex-col`}>
        {/* Logo */}
        <div className="p-6 border-b border-gray-700">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 bg-blue-600 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-xl">T</span>
            </div>
            {!sidebarCollapsed && (
              <span className="text-white font-bold text-xl">TIMORA</span>
            )}
          </div>
        </div>

        {/* Menu Items */}
        <nav className="flex-1 p-4">
          <Link href="/admin/dashboard">
            <div className="flex items-center gap-3 px-4 py-3 rounded-lg cursor-pointer bg-blue-600 text-white mb-2">
              <Home className="w-5 h-5" />
              {!sidebarCollapsed && <span>Dashboard</span>}
            </div>
          </Link>

          <Link href="/admin/products">
            <div className="flex items-center gap-3 px-4 py-3 rounded-lg cursor-pointer text-gray-400 hover:bg-gray-700 hover:text-white mb-2">
              <Package className="w-5 h-5" />
              {!sidebarCollapsed && <span>Products</span>}
            </div>
          </Link>

          <Link href="/admin/orders">
            <div className="flex items-center gap-3 px-4 py-3 rounded-lg cursor-pointer text-gray-400 hover:bg-gray-700 hover:text-white mb-2">
              <ShoppingBag className="w-5 h-5" />
              {!sidebarCollapsed && <span>Orders</span>}
            </div>
          </Link>

        {/*  In admin dashboard or layout*/}
<Link href="/admin/customers">
  <div className="flex items-center gap-3 px-4 py-3 rounded-lg cursor-pointer text-gray-400 hover:bg-gray-700 hover:text-white">
    <Users className="w-5 h-5" />
    {!sidebarCollapsed && <span>Customers</span>}
  </div>
</Link>

          <Link href="/admin/analytics">
            <div className="flex items-center gap-3 px-4 py-3 rounded-lg cursor-pointer text-gray-400 hover:bg-gray-700 hover:text-white mb-2">
              <BarChart3 className="w-5 h-5" />
              {!sidebarCollapsed && <span>Analytics</span>}
            </div>
          </Link>
        </nav>

        {/* Logout Button */}
        <div className="p-4 border-t border-gray-700">
          <button
            onClick={handleLogout}
            className="flex items-center gap-3 px-4 py-3 rounded-lg cursor-pointer text-gray-400 hover:bg-gray-700 hover:text-white w-full"
          >
            <LogOut className="w-5 h-5" />
            {!sidebarCollapsed && <span>Logout</span>}
          </button>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 overflow-auto">
        {/* Header */}
        <div className="bg-[#2a2d47] p-6 flex justify-between items-center">
          <h1 className="text-2xl font-bold text-white">Dashboard</h1>
          <div className="flex items-center gap-4">
            <button className="bg-[#1a1d2e] px-4 py-2 rounded-lg text-white text-sm flex items-center gap-2">
              <Calendar className="w-4 h-4" />
              {selectedPeriod}
              <ChevronDown className="w-4 h-4" />
            </button>
          </div>
        </div>

        <div className="p-6">
          {/* Stats Cards */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
            {/* Today's Sales */}
            <div className="bg-[#2a2d47] rounded-xl p-6">
              <div className="flex justify-between items-start">
                <div>
                  <p className="text-gray-400 text-sm mb-1">Today,s Sales</p>
                  <h3 className="text-3xl font-bold text-white">
                    ৳{((stats?.today_sales || 0) / 1000).toFixed(1)}K
                  </h3>
                  <p className="text-gray-400 text-sm mt-2">
                    We have sold {stats?.total_orders || 0} items
                  </p>
                </div>
                <div className="relative w-20 h-20">
                  <svg className="w-20 h-20 transform -rotate-90">
                    <circle cx="40" cy="40" r="36" stroke="#374151" strokeWidth="8" fill="none" />
                    <circle cx="40" cy="40" r="36" stroke="#3B82F6" strokeWidth="8" fill="none"
                      strokeDasharray={`${2 * Math.PI * 36 * 0.7} ${2 * Math.PI * 36}`} />
                  </svg>
                </div>
              </div>
            </div>

            {/* Today's Revenue */}
            <div className="bg-[#2a2d47] rounded-xl p-6">
              <div className="flex justify-between items-start">
                <div>
                  <p className="text-gray-400 text-sm mb-1">Total Revenue</p>
                  <h3 className="text-3xl font-bold text-white">
                    ৳{((stats?.total_revenue || 0) / 1000).toFixed(1)}K
                  </h3>
                  <p className="text-gray-400 text-sm mt-2">Available to payout</p>
                </div>
                <div className="relative w-20 h-20">
                  <svg className="w-20 h-20 transform -rotate-90">
                    <circle cx="40" cy="40" r="36" stroke="#374151" strokeWidth="8" fill="none" />
                    <circle cx="40" cy="40" r="36" stroke="#10B981" strokeWidth="8" fill="none"
                      strokeDasharray={`${2 * Math.PI * 36 * 0.4} ${2 * Math.PI * 36}`} />
                  </svg>
                </div>
              </div>
            </div>

            {/* Total Products */}
            <div className="bg-[#2a2d47] rounded-xl p-6">
              <div className="flex justify-between items-start">
                <div>
                  <p className="text-gray-400 text-sm mb-1">Total Products</p>
                  <h3 className="text-3xl font-bold text-white">{stats?.total_products || 0}</h3>
                  <p className="text-gray-400 text-sm mt-2">Active in inventory</p>
                </div>
                <div className="relative w-20 h-20">
                  <svg className="w-20 h-20 transform -rotate-90">
                    <circle cx="40" cy="40" r="36" stroke="#374151" strokeWidth="8" fill="none" />
                    <circle cx="40" cy="40" r="36" stroke="#F59E0B" strokeWidth="8" fill="none"
                      strokeDasharray={`${2 * Math.PI * 36 * 0.6} ${2 * Math.PI * 36}`} />
                  </svg>
                </div>
              </div>
            </div>
          </div>

          {/* Quick Stats */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
            <div className="bg-[#2a2d47] rounded-xl p-4 flex items-center gap-4">
              <div className="w-12 h-12 bg-blue-500/20 rounded-lg flex items-center justify-center">
                <Users className="w-6 h-6 text-blue-500" />
              </div>
              <div>
                <p className="text-gray-400 text-xs">Total Users</p>
                <p className="text-white text-xl font-bold">{stats?.total_users || 0}</p>
              </div>
            </div>

            <div className="bg-[#2a2d47] rounded-xl p-4 flex items-center gap-4">
              <div className="w-12 h-12 bg-green-500/20 rounded-lg flex items-center justify-center">
                <ShoppingBag className="w-6 h-6 text-green-500" />
              </div>
              <div>
                <p className="text-gray-400 text-xs">Total Orders</p>
                <p className="text-white text-xl font-bold">{stats?.total_orders || 0}</p>
              </div>
            </div>

            <div className="bg-[#2a2d47] rounded-xl p-4 flex items-center gap-4">
              <div className="w-12 h-12 bg-yellow-500/20 rounded-lg flex items-center justify-center">
                <Package className="w-6 h-6 text-yellow-500" />
              </div>
              <div>
                <p className="text-gray-400 text-xs">Products</p>
                <p className="text-white text-xl font-bold">{stats?.total_products || 0}</p>
              </div>
            </div>

            <div className="bg-[#2a2d47] rounded-xl p-4 flex items-center gap-4">
              <div className="w-12 h-12 bg-purple-500/20 rounded-lg flex items-center justify-center">
                <TrendingUp className="w-6 h-6 text-purple-500" />
              </div>
              <div>
                <p className="text-gray-400 text-xs">Growth</p>
                <p className="text-white text-xl font-bold">+15%</p>
              </div>
            </div>
          </div>

          {/* Latest Orders Table */}
          <div className="bg-[#2a2d47] rounded-xl p-6">
            <div className="flex justify-between items-center mb-6">
              <h3 className="text-xl font-bold text-white">Latest Orders</h3>
              <Link href="/admin/orders" className="text-blue-500 hover:text-blue-400 text-sm">
                View All Orders →
              </Link>
            </div>

            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="text-gray-400 text-sm">
                    <th className="text-left py-3 px-4">Order ID</th>
                    <th className="text-left py-3 px-4">Date</th>
                    <th className="text-left py-3 px-4">Customer</th>
                    <th className="text-left py-3 px-4">Status</th>
                    <th className="text-left py-3 px-4">Amount</th>
                    <th className="text-left py-3 px-4">Action</th>
                  </tr>
                </thead>
                <tbody>
                  {recentOrders.length > 0 ? (
                    recentOrders.slice(0, 10).map((order) => (
                      <tr key={order.order_id} className="border-t border-gray-700">
                        <td className="py-4 px-4 text-white font-medium">{order.order_id}</td>
                        <td className="py-4 px-4 text-gray-400">
                          {new Date(order.created_at).toLocaleDateString()}
                        </td>
                        <td className="py-4 px-4 text-white">
                          {order.customer_name || order.user_email || 'N/A'}
                        </td>
                        <td className="py-4 px-4">
                          <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs ${getStatusColor(order.order_status)}`}>
                            <span className={`w-2 h-2 rounded-full mr-2 ${getStatusDotColor(order.order_status)}`}></span>
                            {order.order_status}
                          </span>
                        </td>
                        <td className="py-4 px-4 text-white">৳{order.total_amount.toLocaleString()}</td>
                        <td className="py-4 px-4">
                          <button className="text-gray-400 hover:text-white">
                            <MoreVertical className="w-5 h-5" />
                          </button>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan={6} className="py-8 text-center text-gray-400">
                        No orders found
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}